
#refreshIndex{position:absolute;left:0;top:0;width:100%;padding-top:.05rem;overflow:hidden
}
#refreshIndex .spinner{max-width:.7rem;max-height:.7rem;position:relative;margin:0 auto
}
#refreshIndex p{margin-top:10px;font-size:.16rem;color:#666;text-align:center
}
.double-bounce1,.double-bounce2{width:100%;height:100%;border-radius:50%;background-color:#67CF22;opacity:0.6;position:absolute;top:0;left:0;animation:bounce 2.0s infinite ease-in-out
}
.double-bounce2{animation-delay:-1.0s
}
@keyframes bounce{
0%,100%{transform:scale(0);-webkit-transform:scale(0)
}
50%{transform:scale(1);-webkit-transform:scale(1)
}
}



